body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  min-height: 100vh;
}

.App {
  text-align: center;
}

.App-header {
  background-image: -webkit-linear-gradient(#282c34 80%, #393F4A);
  background-image: linear-gradient(#282c34 80%, #393F4A);
  min-height: 10vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #636D80;
}

.App-body {
  background-color: #393F4A;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  text-align: center;
  min-height: 85vh;
}
.App-footer {
  background-image: -webkit-linear-gradient(#393F4A, #282c34);
  background-image: linear-gradient(#393F4A, #282c34);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(3px + 1vmin);
  text-align: left;
  min-height: 5vh;
  padding-left: 2vw;
  color: #636D80;
}

.Elevator-img {
  padding: 2%;
  position: relative;
  max-width: 15%;
  max-height: 15%;
  pointer-events: none;
}

.Floor-Button {
  padding-top: 2%;
  padding-bottom: 2%;
  padding-left: 4.5%;
  padding-right: 4.5%;
  position: relative;
  max-width: 15%;
  max-height: 15%;
}

.Highlighted-Floor-Button {
  padding-top: 2%;
  padding-bottom: 2%;
  padding-left: 4.5%;
  padding-right: 4.5%;
  position: relative;
  margin-left: -15%;
  opacity: 0;
  max-width: 15%;
  max-height: 15%;
}

.Highlighted-Panel-Button {
  padding: 5% 10%;
  padding-left: 4.5%;
  padding-right: 4.5%;
  position: relative;
  margin-left: -34%;
  opacity: 0;
}

.Down-Button {
  padding: 4%;
  max-width: 45%;
  max-height: 45%;
  position: relative;

}

.Up-Button {
  padding: 4%;
  max-width: 45%;
  max-height: 45%;
  position: relative;
  top: 0;
  left: 0;
}

.Highlighted-Up-Button {
  padding: 4%;
  max-width: 45%;
  max-height: 45%;
  opacity: 0;
  position: relative;
  margin-left: -45%;
  top: 0;
  left: 0;
}

.Highlighted-Down-Button {
  padding: 4%;
  max-width: 45%;
  max-height: 45%;
  opacity: 0;
  position: relative;
  margin-left: -45%;
  top: 0;
  left: 0;
}

.Panel-Button {
  margin: 10%;
  margin-left: 10%;
  margin-right: 5%;
}

.Extern-Component {
  width: inherit;
}

.myParagraph {
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 1vmin);
  color: #636D80;
  text-align: left;
  padding-top: 0%;
  padding-bottom: 6%;
}



